import { Zero } from '..';
import flatten from 'lodash-es/flatten';
import chunk from 'lodash-es/chunk';
export const HelperPlugin = new class {
    install() {
        Object.defineProperty(Zero.prototype, '$helper', {
            get() {
                return new ApiHelper(this);
            }
        });
    }
}();
class ApiHelper {
    constructor(m) {
        this.m = m;
    }
    deleteMember(uin) {
        this.useM((m, e) => {
            m.setType(12);
            m.setGroupId(e.groupid);
            m.setUin(uin);
        });
    }
    sendText(text, type, uin) {
        this.useM((m, e) => {
            type = type || 0;
            m.setType(type);
            m.setGroupId(e.groupid);
            if (uin)
                m.setUin(uin);
            m.add('msg', text);
        });
    }
    targetedText(text, target) {
        this.useM((m, e) => {
            m.setType(0);
            m.setGroupId(e.groupid);
            const t = chunk(flatten(target), 2);
            t.forEach(v => m.add('at', v.join('@') + ' '));
            m.add('msg', text);
        });
    }
    sendXML(xml, type, uin) {
        this.useM((m, e) => {
            type = type || 0;
            m.setType(type);
            m.setGroupId(e.groupid);
            if (uin)
                m.setUin(uin);
            m.add('xml', xml);
        });
    }
    sendJSON(json, type, uin) {
        this.useM((m, e) => {
            type = type || 0;
            m.setType(type);
            m.setGroupId(e.groupid);
            if (uin)
                m.setUin(uin);
            m.add('json', json);
        });
    }
    sendImg(url, type, uin) {
        this.useM((m, e) => {
            type = type || 0;
            m.setType(type);
            m.setGroupId(e.groupid);
            if (uin)
                m.setUin(uin);
            m.add('img', url);
        });
    }
    sendPtt(url, type, uin) {
        this.useM((m, e) => {
            type = type || 0;
            m.setType(type);
            m.setGroupId(e.groupid);
            if (uin)
                m.setUin(uin);
            m.add('ptt', url);
        });
    }
    like(uin) {
        this.useM((m, e) => {
            uin = uin || e.uin;
            m.setType(8);
            m.setUin(uin);
        });
    }
    updateProfile(uin, profile) {
        this.useM((m, e) => {
            m.setType(9);
            m.setGroupId(e.groupid);
            m.setUin(uin);
            m.setTitle(profile);
        });
    }
    muteMember(uin, seconds) {
        this.useM((m, e) => {
            m.setType(10);
            m.setGroupId(e.groupid);
            m.setUin(uin);
            m.setValue(seconds);
        });
    }
    muteGroup(on) {
        this.useM((m, e) => {
            m.setType(11);
            m.setGroupId(e.groupid);
            m.setValue(on ? 0 : 1);
        });
    }
    useM(func) {
        this.m.$api.clearMsg();
        func(this.m.$api, this.m.$event);
        this.m.$api.send();
    }
}
