import { Zero } from '..';
export const StorePlugin = new class {
    install() {
        Object.defineProperty(Zero.prototype, '$store', {
            get() {
                return Store.getInstance(this.$api);
            }
        });
    }
}();
class Store {
    constructor() {
        this.map = {};
    }
    static getInstance(api) {
        Store.instance.api = api;
        return Store.instance;
    }
    init(name) {
        const load = (key) => {
            this.map[key] = JSON.parse((api => api.read(`${api.getRootPath()}/SQ/Zero/${key}.store`))(this.api) || '{}');
        };
        if (name) {
            load(name);
        }
        else {
            Object.keys(this.map).forEach(v => {
                load(v);
            });
        }
    }
    update(name, func) {
        if (func) {
            if (!this.map[name])
                this.init(name);
            func(this.map[name]);
            this.updateFile(name);
        }
        else {
            this.map[name] = undefined;
        }
    }
    use(name, func) {
        if (!this.map[name])
            this.init(name);
        return func(this.map[name]);
    }
    updateFile(name) {
        (api => {
            api.write(`${api.getRootPath()}/SQ/Zero/${name}.store`, JSON.stringify(this.map[name]));
        })(this.api);
    }
}
Store.instance = new Store();
